function setMarker(latlng: L.LatLngExpression) {
  console.log(L.Icon.Default.prototype.options);
  const icon = L.icon({
    ...L.Icon.Default.prototype.options,
    iconRetinaUrl: "https://unpkg.com/leaflet@1.9.4/dist/images/marker-icon-2x.png",
